.report-block{
	&__ct{
		margin-bottom: 50px;
	}


	&__item{
		@include flex();
		border-bottom: 1px solid #E0E1E3;
		@include font-size(14);
		font-weight: 500;
		line-height: 1.785;
		padding: 13px 0 13px 23px;
		position: relative;
		&.has-card{
			border-bottom: none;
		}
		&.show{
			padding: 0;
		}
		&-ttl{
			width: 203px;
			font-weight: 700;
		}
		&-ct{
			width: calc(100% - 203px);
			p{
				margin-bottom: 25px;
				&:last-of-type{
					margin-bottom: 0;
				}
			}
			.statuschips{
				position: absolute;
				right: 10px;
				bottom: 0;
			}
			.col2_block_side_user_star_icn{
				margin-bottom: 17px;
			}
		}
		&-wp{
			position: relative;
			padding-right: 95px;
		}
	}
	&__gallery{
		@include flex();
		margin-top: 20px;
		margin-bottom: 15px;
		&-item{
			width: 250px;
			margin-right: 25px;
			&:last-of-type{
				margin-right: 0;
			}
		}
		&--02{
			.report-block__gallery-item{
				width: 190px;
			}
			.report-block__gallery-thumb{
				margin-bottom: 10px;
			}
		}
		&--03{
			margin-top: 5px;
			margin-bottom: 5px;
		}
	}
}


.report-steps{
	@include flex();
	margin-bottom: 30px;
	&__item{
		min-width: 200px;
		border-bottom: 5px solid #E0E1E3;
		a{
			display: block;
			text-decoration: none;
			@include font-size(14.5);
			font-weight: 700;
			padding: 0 15px 5px;
		}
		&.is-active{
			border-color: #5BC0DE;
			a{
				color: #5BC0DE;
				pointer-events: none;
			}
		}
	}
}
