.result-block{
	&__ct{
		
	}
	&__table{
		@include font-size(14);
		font-weight: 500;
		text-align: left;
		width: 100%;
		tr{
			th, td{
				padding: 13px 0;
				border-bottom: 1px solid #E0E1E3;
			}
			th{
				width: 180px;
				font-weight: 700;
				padding-left: 22px;
				vertical-align: top;
			}
			td{
				width: calc(100% - 180px);
			}
			.looksort_table_total{
				@include font-size(26);
				font-weight: 700;
				padding-top: 5px;
				padding-bottom: 2px;
			}
		}
	}
}