@charset "utf-8";
@import "vars";
/* ====================================================
T055、T056、T057
==================================================== */
.daily {
	display: flex;
	margin-bottom: 50px;
}
.daily * {
	box-sizing: border-box;
}
.daily_data {
	display: flex;
	align-items: center;
	width: 16%;
	min-width: 200px;
	height: 160px;
	margin-right: 1%;
}
.daily_data .prev, .daily_data .next {
	display: block;
	border-right: 2px solid #464646;
	border-bottom: 2px solid #464646;
	width: 8px;
	height: 8px;
}
.daily_data .prev {
	margin-right: 5px;
	//transform: translateY(-50%) rotate(135deg);
	transform: rotate(135deg);
}
.daily_data .next {
	margin-left: 5px;
	transform: rotate(315deg);
}
.daily_data_inr {
	max-width: calc(100% - 26px);
	width: 240px;
	height: 160px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #F2F0EE;
	border-radius: 5px;
	border: 1px solid #E0E1E3;
	font-weight: bold;
	line-height: 1.7;
	text-align: center;
}
.daily_data_num {
	font-size: 2.1rem;
}
.daily_data_week {
}
.daily_look{
	width: 83%;
	display: flex;
	flex-wrap: wrap;
	margin-right: -1.5%;
}
.daily_look_box{
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	width: 18.5%;
	min-width: 200px;
	height: 160px;
	border: 1px solid #E0E1E3;
	padding: 1%;
	margin: 0 1.5% 1.5% 0;
}
.daily_look_box_ttl{
	width: 100%;
	font-size: 1.4rem;
	font-weight: bold;
}
.daily_look_box_amount{
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 3% 1%;
}
.daily_look_box_amount_total{
	font-size: 4rem;
	font-weight: bold;
	color: #5BC0DE;
}
.daily_look_box_amount_item > span{
	display: flex;
	justify-content: space-between;
}
.daily_look_box_amount_item > span > span:first-child{
	display: block;
	margin-right: 1em;
}
.daily_look_box_look{
	width: 100%;
	display: block;
	text-align: center;
	border-top: 1px solid #E0E1E3;
	color: #969696;
	font-size: 1.3rem;
	line-height: 1;
	padding-top: 5%;
	text-decoration: none;
}
.reminder{
	margin-bottom: 50px;
}
.reminder_ttlbox{
	display: flex;
	align-items: center;
	margin-bottom: .5em;
}
.reminder_ttlbox .page_ttl_02{
	margin-right: .5em;
}
.todolist{
	margin-bottom: 50px;
}
.todolist_ttlbox{
	display: flex;
	justify-content: space-between;
	margin-bottom: .5em;
}
.todolist_table_priority,
.todolist_table_status{
	width: 160px;
}
.todolist_table_person{
	width: 210px;
}
.todolist_table_person .tableform_select{
	width: 165px;
}
.todolist_table_btn{
	width:180px;
}
.todolist_table_btn .btn_area{
	justify-content: flex-end;
}
.receivingelectricity_ttlbox{
	display: flex;
	justify-content: space-between;
	margin-bottom: .5em;
}
.receivingelectricity_ttlbox .page_ttl_02{
	width: 165px;
}
.receivingelectricity_ttlbox .tableform_select{
	width: 165px;
	margin: 0 5px;
}
.receivingelectricity_ttlbox_inr{
	width: calc(100% - 265px);
	display: flex;
}
.receivingelectricity_ttlbox_inr > span{
	display: flex;
	align-items: center;
}
.receivingelectricity_ttlbox_inr > span + span {
	margin-left: 2em;
}
.receivingelectricity_ttlbox_inr .btn_style_01{
	margin-left: .5em;
}
.receivingelectricity_table_data{
	width: 145px;
}
.receivingelectricity_table_author{
	width: 135px;
}
.receivingelectricity_table_title{
	width: 250px;
}
.receivingelectricity_table_sender{
	width: 135px;
}
.receivingelectricity_table_time{
	width: 160px;
}
.looksort_table{
	// min-width: 1580px;
	min-width: 1530px;
}
.looksort_table thead th,
.looksort_table tbody td {
	padding: 0.8% 1%;
	vertical-align: middle;
}
.looksort_table_id{
	width: 100px;
}
.looksort_table_start{
	width: 200px;
}
.looksort_table_end{
	width:200px;
}
.looksort_table_supporter{
	width: 170px;
}
.looksort_table_user{
	width: 170px;
}
.looksort_table_time{
	width: 80px;
}
.looksort_table_type{
	width: 138px;
}
.looksort_table_divisions{
	width: 100px;
}
.looksort_table_watching{
	width: 100px;
}
.looksort_table_status{
	width: 120px;
}
.looksort_table_memo{
}
.looksort_statusdescription{
	width: 100%;
	max-width: 1300px;
}

