.add-form{
	&__item{
		padding: 25px 0;
		@include flex(0,0, wrap);
		border-bottom: 1px solid #E0E1E3;
		&:last-of-type{
			margin-bottom: 50px;
		}
		&.add{
			.add-form__ct--remove{
				.btn_style_02{
					display: flex;
				}
			}
		}
		&--center{
			align-items: center;
			padding: 0.8% 0;
			.add-form__title{
				top: 0;
			}
		}
	}
	&__title{
		font-weight: 700;
		width: 230px;
		box-sizing: border-box;
		padding-left: 22px;
		position: relative;
		top: -6px;
	}
	&__ct{
		width: calc(100% - 230px);
		position: relative;
		&--remove{
			margin-left: auto;
			margin-right: 0;
			//margin-top: 15px;
			//@include flex();
			.btn_style_03{
				margin-right: 15px;
				margin-top: 15px;
			}
			.btn_style_02{
				display: none;
				margin: 0 auto;
				left: -130px;
				position: relative;
			}
		}
		.btn_style_03{
		
		}
	}
	&__input-group{
		@include flex();
		margin-top: 7px;
		padding-left: 8px;
		.tableform_select{
			width: 80px;
			margin-right: 16px;
			&:last-of-type{
				margin-right: 0;
			}
		}
		&--styles02{
			padding-left: 0;
			margin-top: 0;
			margin-left: 26px;
		}
	}
	&__textarea{
		margin-bottom: 13px;
		max-width: 1209px;
		textarea{
			width: 100%;
			height: 190px;
		}
	}
	&__file{
		//width: 910px !important;
		padding-left: 0 !important;
		max-width: 1209px;
		width: 100% !important;
		label{
			margin-bottom: 0;
		}
	}
	&__control{
		position: relative;
		.btn_style_04{
			position: absolute;
			left: 10px;
			top: 0;
		}
		&--styles02{
			.btn_add{
				margin-bottom: 30px;
			}
			.btn_style_04{
				position: static;
			}
		}
	}
}


.btn_add{
	font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
	width: 198px;
	height: 40px;
	@include flex(center, center);
	border-radius: 6px;
	background: #F2F0EE;
	color: #464646;
	position: relative;
	margin: 0 auto;
	border: none;
	outline: 0;
	@include font-size(14.5);
	font-weight: 700;
	cursor: pointer;
	padding-left: 30px;
	left: -44px;
	&:hover{
		opacity: 0.7;
		@include smooth-transition;
	}
	@include p-before(32px, 32px){
		background: url("/assets/img/common/ico-plus.png") no-repeat center/100%;
		top: calc(50% - 16px);
		left: 4px;
	}
}



.move-child{
	display: none;
}


#form-edit{
	.form_part_btn01{
		display: none;
	}
}
